function proxyData(vm,target,key) {
    Object.defineProperty(vm,key,{
        get(){
            console.log("代理拦截get");
            return vm[target][key];
        },
        set(newValue){
            console.log("代理拦截set");
            vm[target][key] = newValue; 
        }
    })
}

export default proxyData;